import React from "react";
import {connect} from "react-redux";

import OraEmployeeList from "eshop/components/employee/OraEmployeeList";
import {OraButton, OraLoader} from "eshop/components/OraCommonComponents";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {getEmployeeList, getAssistModeModalStateData, getEmployeeListFilters, isLoadingEmployeeList, getSelectedEmployee, getCurrentPage, getFilterEnabled} from "../../selectors";
import {closeAssistModeModal,openConfirmationModal, getEmployeeListByFilters, setSelectedEmployee, setPage, setFilterEnabled} from "eshop/modules/checkout/actions/assistMode";

class OraCheckoutAssistModePopup extends React.Component {
    constructor() {
        super();
    }

    componentDidMount() {
        OPL.UI.loadModules(document.getElementById("employee-list-loader-component"), {
            path: "core/modules/loader",
            options: "{\"fadeDuration\":120}",
        });
    }

    openAssistModeEnterConfirmationPopup() {
        this.props.closeAssistModeModal();
        this.props.openConfirmationModal();
    }

    validateFilters() {
        return (
            this.validateNotEmptyTextFilter(this.refs.filterLastName.value) ||
            this.validateNotEmptyTextFilter(this.refs.filterLoginOtsa.value) ||
            this.validateNotEmptyTextFilter(this.refs.filterFullBscs.value) ||
            this.validateNotEmptyNumericFilter(this.refs.filterSisId.value)
        )
    }

    validateNotEmptyTextFilter(value) {
        value = value.trim();

        return value.length >= this.props.minTextFilterChars;
    }

    validateNotEmptyNumericFilter(value) {
        return value.trim().length > 0 && !isNaN(value) && isFinite(value);
    }

    doFilterOnEnter(e) {
        if (e.key === "Enter") {
            this.doFilter();
        }
    }

    btnFilterClick() {
        this.doFilter();
    }

    doFilter() {
        if (!this.validateFilters()) return;
        this.searchEmployees(0);
    }

    btnNextClick() {
        let newPage = this.props.page + 1;
        this.searchEmployees(newPage);
    }

    btnPrevClick() {
        let newPage = this.props.page - 1;
        this.searchEmployees(newPage);
    }

    clearFilters() {
        this.refs.filterLastName.value = "";
        this.refs.filterName.value = "";
        this.refs.filterLocation.value = "";
        this.refs.filterLoginOtsa.value = "";
        this.refs.filterFullBscs.value = "";
        this.refs.filterSisId.value = "";

        this.onFilterChange();
    }

    searchEmployees(page) {

        const filters = {
            lastName: this.prepareFilterLike(this.refs.filterLastName.value),
            name: this.prepareFilterLike(this.refs.filterName.value),
            location: this.prepareFilterLike(this.refs.filterLocation.value),
            loginOtsa: this.prepareFilterLike(this.refs.filterLoginOtsa.value),
            fullBscs: this.prepareFilterLike(this.refs.filterFullBscs.value),
            sisId: this.refs.filterSisId.value.trim(),
            offset: page * this.props.employeeListPageSize,
            limit: this.props.employeeListPageSize + 1,
        };
        this.props.setPage(page);
        this.props.getEmployeeListByFilter(filters);
    }

    checkPrevButton(){
        return this.props.page === 0 || this.props.isLoadingInProgress;
    }

    checkNextButton(){
        let storeList = this.props.employeeList;
        let tableList = this.props.employeeList;

        if (tableList) {
            tableList = tableList.slice(0, this.props.employeeListPageSize);
        }

        let storeListLen = (storeList) ? storeList.length : 0;
        let tableListLen = (tableList) ? tableList.length : 0;
        return !(tableListLen < storeListLen);
    }

    prepareFilterLike(value) {
        const v = value.trim();

        return v.length > 0 ? "%" + v + "%" : v;
    }

    onSelectionChanged(sisId) {
        this.props.setSelectedEmployee(sisId);
    }

    isEmployeeSelected() {
        return !this.props.selectedEmployee;
    }

    onFilterChange() {
        this.props.setFilterEnabled(this.validateFilters());
    }

    renderEmployeeList() {
        if (!this.props.employeeList) {
            return (
                    <div className="l-col l-col-12">
                        <div className="opl-msg opl-msg--info opl-msg--context">
                            <p>{this.props.descriptions.employeeListHint}</p>
                        </div>
                    </div>
            )
        } else if (this.props.employeeList.length > 0) {
            return (
                    <div className="l-col l-col-12">
                        <OraEmployeeList employees={this.props.employeeList} id="assist-mode-popup-employeeList"
                                         onSelectionChanged={this.onSelectionChanged.bind(this)}
                                         listId={"assist-mode-employeeList"} />
                        <div className="l-row">
                            <div className="l-col l-col-1 l-col-small-6 u-right">
                                <OraButton type="secondary" size="small" className="u-w-100"
                                           disabled={this.checkNextButton()}
                                           onClick={this.btnNextClick.bind(this)}>
                                    &gt;
                                </OraButton>
                            </div>
                            <div className="l-col l-col-1 l-col-small-6 u-right">
                                <OraButton type="secondary" size="small" className="u-w-100"
                                           disabled={this.checkPrevButton()}
                                           onClick={this.btnPrevClick.bind(this)}>
                                    &lt;
                                </OraButton>
                            </div>
                        </div>
                    </div>
            )
        } else {
            return (
                    <div className="l-col l-col-12">
                        <div className="opl-msg opl-msg--info opl-msg--context">
                            <p>{this.props.descriptions.searchResultEmptyHint}</p>
                        </div>
                    </div>
            )
        }
    }

    renderFilterHint() {
        if (!this.props.btnFilterEnabled) {
            return (
                <div className="opl-msg opl-msg--info opl-msg--context">
                    <p>{this.props.descriptions.filterHint}</p>
                </div>
            );
        }
    }

    render() {
        return (
            <OraModal id={"ora-checkout-assist-mode-panel-modal"}
                      showClose={true} open={this.props.assistModeModalState} onClose={this.props.closeAssistModeModal.bind(this)}>
                <div className="l-full-row">
                    <div className="l-page-row">
                        <div className="opl-form">
                            <div className="l-row u-spacing">
                                <div className="l-col l-col-12 u-font-bold opl-msg">
                                    <h3>{this.props.descriptions.assistModeSearch}</h3>
                                </div>
                            </div>
                            <div className="l-row">
                                <div className="l-col l-col-2 l-col-small-12">
                                    <label htmlFor="filterLastName">{this.props.descriptions.lastNameSearch}</label>
                                    <input id="filterLastName" ref="filterLastName" type="text"
                                           onChange={this.onFilterChange.bind(this)}
                                           onKeyPress={this.doFilterOnEnter.bind(this)}/>
                                </div>
                                <div className="l-col l-col-2 l-col-small-12">
                                    <label htmlFor="filterName">{this.props.descriptions.firstNameSearch}</label>
                                    <input id="filterName" ref="filterName" type="text"
                                           onChange={this.onFilterChange.bind(this)}
                                           onKeyPress={this.doFilterOnEnter.bind(this)}/>
                                </div>
                                <div className="l-col l-col-2 l-col-small-12">
                                    <label htmlFor="filterLocation">{this.props.descriptions.localizationSearch}</label>
                                    <input id="filterLocation" ref="filterLocation" type="text"
                                           onChange={this.onFilterChange.bind(this)}
                                           onKeyPress={this.doFilterOnEnter.bind(this)}/>
                                </div>
                                <div className="l-col l-col-2 l-col-small-12">
                                    <label htmlFor="filterLoginOtsa">{this.props.descriptions.ostaSearch}</label>
                                    <input id="filterLoginOtsa" ref="filterLoginOtsa" type="text"
                                           onChange={this.onFilterChange.bind(this)}
                                           onKeyPress={this.doFilterOnEnter.bind(this)}/>
                                </div>
                                <div className="l-col l-col-2 l-col-small-12">
                                    <label htmlFor="filterFullBscs">{this.props.descriptions.bscsSearch}</label>
                                    <input id="filterFullBscs" ref="filterFullBscs" type="text"
                                           onChange={this.onFilterChange.bind(this)}
                                           onKeyPress={this.doFilterOnEnter.bind(this)}/>
                                </div>
                                <div className="l-col l-col-2 l-col-small-12">
                                    <label htmlFor="filterSisId">{this.props.descriptions.salesmanIdSearch}</label>
                                    <input id="filterSisId" ref="filterSisId" type="text"
                                           onChange={this.onFilterChange.bind(this)}
                                           onKeyPress={this.doFilterOnEnter.bind(this)}/>
                                </div>
                            </div>
                            <div className="l-row">
                                <div className="l-col l-col-8 l-col-small-12">
                                    {this.renderFilterHint()}
                                </div>
                                <div className="l-col l-col-2 l-col-small-12 u-right">
                                    <OraButton onClick={this.btnFilterClick.bind(this)} type="primary" size="medium"
                                               disabled={this.props.isLoadingInProgress || !this.props.btnFilterEnabled} className="u-w-100">
                                        {this.props.descriptions.filterButtonLabel}
                                    </OraButton>
                                </div>
                                <div className="l-col l-col-2 l-col-small-12 u-right">
                                    <OraButton onClick={this.clearFilters.bind(this)} type="secondary" size="medium" className="u-w-100">
                                        {this.props.descriptions.clearButtonLabel}
                                    </OraButton>
                                </div>
                            </div>
                            <div className="o-separator o-separator--l o-separator--expanded"/>
                            <OraLoader className="l-row" loading={this.props.isLoadingInProgress} id="employee-list-loader-component" parentId="assist-mode-loaders" size="l" >
                                    {this.renderEmployeeList()}
                            </OraLoader>
                            <div className="o-separator o-separator--l o-separator--expanded"/>
                            <div className="l-row">
                                <div className="l-col l-col-2 l-col-small-12 u-right">
                                    <OraButton type="primary" size="medium" className="u-w-100 opl-btn--green"
                                               disabled={this.isEmployeeSelected()}
                                               onClick={this.openAssistModeEnterConfirmationPopup.bind(this)}>
                                        {this.props.descriptions.enterButtonLabel}
                                    </OraButton>
                                </div>
                                <div className="l-col l-col-2 l-col-small-12 u-right">
                                    <OraButton modal={true} type="secondary" size="medium" className="u-w-100">
                                        {this.props.descriptions.cancelButtonLabel}
                                    </OraButton>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </OraModal>
        );
    }
}

const mapDispatchToProps = dispatch => ({
    closeAssistModeModal: () => dispatch(closeAssistModeModal()),
    openConfirmationModal: () => dispatch(openConfirmationModal()),
    getEmployeeListByFilter: (filters) => dispatch(getEmployeeListByFilters(filters)),
    setSelectedEmployee: (employee) => dispatch(setSelectedEmployee(employee)),
    setPage: (page) => dispatch(setPage(page)),
    setFilterEnabled: (state) => dispatch(setFilterEnabled(state)),
});

const mapStateToProps = state => ({
    assistModeModalState: getAssistModeModalStateData(state),
    employeeList: getEmployeeList(state),
    filters: getEmployeeListFilters(state),
    isLoadingInProgress: isLoadingEmployeeList(state),
    selectedEmployee: getSelectedEmployee(state),
    page: getCurrentPage(state),
    btnFilterEnabled: getFilterEnabled(state),
});

export default connect(mapStateToProps, mapDispatchToProps)(OraCheckoutAssistModePopup);


OraCheckoutAssistModePopup.defaultProps = {
    onEnter: function(event) {},
    employeeListPageSize: 10,
    minTextFilterChars: 3,
};
